import React, { Component } from "react";
// import { Form, Alert, Container, Row, Col, CardGroup, Card, CardBody, Button, InputGroup, InputGroupAddon, Input } from "reactstrap";
import Header from '../../Components/Header/index'
import Footer from '../../Components/Footer/index'
import { Switch, Route } from 'react-router-dom';
class Full extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isFetching: false
        };
    }

    render() {
        return (
            <div>
                <Header />
                <Switch>
                    <Route path="/news" component={Header} />
                    <Route path="/sports" component={Footer} />
                </Switch>
            </div>
        );
    }
}

export default Full;


