const axios = require('axios')
export const Types = {
  AUTH_LOGIN_REQUEST: "AUTH_LOGIN_REQUEST",
  AUTH_LOGIN_SUCCESS: "AUTH_LOGIN_SUCCESS",
  AUTH_LOGIN_FAILURE: "AUTH_LOGIN_FAILURE",

  AUTH_REGISTER_REQUEST: "AUTH_REGISTER_REQUEST",
  AUTH_REGISTER_SUCCESS: "AUTH_REGISTER_SUCCESS",
  AUTH_REGISTER_FAILURE: "AUTH_REGISTER_FAILURE",

  AUTH_LOGOUT: "AUTH_LOGOUT",

};

export function loginRequest(userName, passWord) {
  return (dispatch, getState) => {
    const action = {
      type: Types.AUTH_LOGIN_REQUEST
    };
    dispatch(action);
    setTimeout(() => {
      dispatch(fetchLogin(userName, passWord));
    }, 300);
  };
}
function getCookie(cname) {
  var name = cname + "=";
  var decodedCookie = decodeURIComponent(document.cookie);
  var ca = decodedCookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}
function fetchLogin(userName, passWord) {
  return dispatch => {
    axios.post('http://35.169.200.202:8080/auth/login', { userName, passWord }, {
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'Access-Control-Allow-Origin': '*',
      }
    })
      .then(response => {
        console.log(response)
        if (!response.ok) throw response.data.message;
        return response.data.result;
      })
      .then(result => {
        const { token } = getCookie('token');
        dispatch(setToken(token));
        dispatch({ type: Types.AUTH_LOGIN_SUCCESS, token });
      })
      .catch(error => {
        console.error(error);
        dispatch({ type: Types.AUTH_LOGIN_FAILURE, error });
      });
  };
}
function setToken(token) {
  return dispatch => {
    localStorage.setItem("token", token);
  };
}

export function logout() {
  return dispatch => {
    dispatch(removeToken());
    dispatch({ type: Types.AUTH_LOGOUT });
  };
}

function removeToken() {
  return dispatch => {
    localStorage.removeItem("token");
  };
}


